<template>
    <div id="tab-wrapper">
        <div class="tabs">
          <div class="tab">
            <h2 @click="isTabVisible">X</h2>
              <div class="tab-hero">
                  <img :src="require('/public/browse/' + shows[type].show_content[show].image)" alt="">
                  <a @click="openTrailer">Play</a>
                  <VideoPlayer v-if="isTrailerVisible" :ytLink="'https://www.youtube.com/embed/'+shows[type].show_content[show].trailer+'?autoplay=1&controls=1'" />
                  <h2 v-if="isTrailerVisible" @click="openTrailer">X</h2>
              </div>
              <article>
                  <h3 class="title">{{shows[type].show_content[show].title}}</h3>
                  <h5 class="release-date">{{shows[type].show_content[show].release}}</h5>
                  <p class="desc">{{shows[type].show_content[show].desc}}</p>
              </article>
            </div>
          </div>
        </div> 
</template>

<script>
import VideoPlayer from "../components/VideoPlayer.vue"

export default {
  components: {
    VideoPlayer
  },
  data() {
    return {
      isTrailerVisible: false,
    }
  },
  methods: {
    openTrailer() {
      if (!this.isTrailerVisible) {
        this.isTrailerVisible = true
      } else {
        this.isTrailerVisible = false
      }
    }
  },
  props: {
    type: Number,
    show: Number,
    shows: Object,
    isTabVisible: Function,
  }
}
</script>

<style scoped>
#tab-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 109;
  background-color: #0009;

  
  overflow-y: scroll;
}
.tab {
  width: 100%;
  max-width: 550px;
  background-color: var(--gray);
}
.tab-hero {
  width: 100%;
  position: relative;
}
.tab-hero img {
  width: 100%;
  aspect-ratio: 20/9;
  object-fit: cover;
}
.tab-hero a {
  position: absolute;
  color: #000;
  text-decoration: none;
  left: 10%;
  bottom: 10%;
  padding: 0.5em 2rem;
  background-color: #ffffffb3;
  border-radius: 5px;
  transition: 0.3s;
}
.tab-hero a:hover {
  background-color: #fff;
  box-shadow: 0 0 0 2px var(--gray), 0 0 0 4px #ffffffb3;
}
.tab article {
  color: #fff;
  padding: 1rem 3rem 3rem;
  line-height: 1.2;
}
.tab article h3 {
  font-size: 1.7rem;
  color: var(--red);
}
.tab article h5 {
  font-size: .9rem;
  margin-bottom: 1.5em;
  color: gray;
}
.tab article p {
  line-height: 1.5;
  font-size: 1.2rem;
}

h2 {
  color: white;
  background-color: var(--red);
  padding: .2rem .5rem;
  border-radius: 50%;

  position: fixed;
  top: 2rem;
  right: 2rem;

  cursor: pointer;
}
</style>