<template>
  <div class="sliders">
      <div class="slider" v-for="(type, index) in shows" :key="index">
          <h2 class="slider-heading">{{ sectionTitle[index] }}</h2>
          <div class="slider-wrapper">
              <div class="slider-item" v-on:click="showTab(index, show.id)" v-for="show in type.show_content" :key="show.id">
                  <div class="box-art">
                      <h3 class="box-title">{{ show.title }}</h3>
                      <img :src="require('/public/browse/'+show.image)" alt="">
                  </div>
              </div>
          </div>
      </div>

      <Tabs v-if="isTabVisivle" :type="ttype" :show="tshow" :shows="shows" :isTabVisible="showTab" />
  </div>
  
</template>

<script>
import Tabs from "../components/Tabs.vue"

export default {
    components: {
      Tabs
    },
    data() {
        return {
            shows: [],
            log: console.log,
            ttype: 0,
            tshow: 0,
            isTabVisivle: false
        }
    },
    mounted() {
        fetch('https://my-json-server.typicode.com/Raia17/animes/db')
            .then(res => res.json())
            .then(data => this.shows = data.shows)
            .catch(err => console.log(err.message));
    },
    methods: {
      showTab(type, show) {
        this.ttype = type
        this.tshow = show
        if (this.isTabVisivle === true) {
          this.isTabVisivle = false
        } else if (this.isTabVisivle === false) {
          this.isTabVisivle = true
        }
        this.log(this.ttype, this.tshow)
      },
    },
    props: {
        sectionTitle: Array,
    }
}
</script>

<style>
.slider {
  margin-block: 4rem;
  padding: 0 2%;
}
.slider-heading {
  color: #fff;
  margin-left: 3rem;
  font-size: 2rem;
  margin-block: 1.2rem;
}
.slider-wrapper {
  display: flex;
  justify-content: space-between;
  overflow-y: hidden;
  overflow-x: hidden;
  position: relative;
}
.slider-item {
  cursor: pointer;
}
.box-art {
  width: 11vw;
  aspect-ratio: 9/16;
  position: relative;
}
.box-title {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.85) 80%);
  width: 100%;
  height: 55%;
  padding-inline: 10px;
  color: #fff;
  text-transform: capitalize;
  text-align: center;
  font-size: 1.4rem;
  bottom: 0;
  border-radius: 5px;
}
.box-art > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.slider-handler {
  cursor: pointer;
  position: absolute;
  font-size: 6rem;
  color: #fff;
  height: 100%;
  z-index: 100;
  top: 180px;
  border-radius: 50%;
  text-shadow: 0 0 10px white;
}
.slider-handler.next {
  right: 10px;
}
.slider-handler.previous {
  left: 10px;
}

</style>